html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
*,*:focus,*:hover{
  outline:none;
}
a {
  text-decoration: none;
  color: inherit;
}
p {
  margin: 0;
}
ul,
li {
  list-style: none;
  margin: 0;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #756f6f !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
}
input,
label {
  display: block;
}
ul {
  padding: 0;
}

body {
  margin: 0;
  min-width: 320px;
  overflow-x: hidden;
  position: relative;
  @extend %rb-regular;
  font-size: 15px;
}
button:focus {
  outline: 0;
}
.loading_full_page{
  position: fixed;
  top: 50%;
  left: 50%;
 
}

.page__section {
  position: relative;
  height: 100vh;
  padding: 72px 0 50px;
}
.page__section-half {
  position: relative;
  height: 60vh;
  padding: 45px 0 0;
}
.page__section-onehalf {
  position: relative;
  height: 150vh;
  padding: 45px 0 0;
}
.container {
  margin: 0 auto;
  max-width: 1118px;
}

.title {
  @extend %rb-bold;
  font-size: 18px;
  line-height: 21px;
}

.section-header {
  text-align: center;
  max-width: 470px;
  margin: 0 auto;
}
.section__header-title {
  @extend %rb-bold;
  font-size: 24px;
  line-height: 28px;
}

.section__header-subtext {
  @extend %rb-regular;
  font-size: 15px;
  line-height: 18px;
  padding-top: 15px;
}
.hide{
  display: none;
}

/* BUTTONS */

.btn {
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition-duration: 0.4s;
  padding: 14px 40px;
  @extend %rb-bold;
  margin-right: 15px;

}
.btn-sm {
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition-duration: 0.4s;
  padding: 7px 20px;
  @extend %rb-bold;
  margin-right: 5px;

}
.white-btn {
  background-color: #fffafa;
}

.white-btn:hover {
  background-color: $global-red;
  
  color: #ffffff;
}
.btn:active {
  transform: translateY(4px);
}

.red-btn {
  background-color: $global-red;
  color: #ffffff;
}
.red-btn:hover {
  background-color: $global-red;
  color: #ffffff;
}

/* ============== MODAL / REGISTER ==================== */

.modal {
  position: fixed; 
  z-index: 11; 
  padding-top: 10px; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%; 
  overflow: auto; 
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.6); 

  &-content {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    width: 750px;
    height: 580px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s;
  }
}
  .modal-reg{ /* "-reg" used both LOGIN & REGISTER modals its looks the same */
  &__container {
    display: flex;
    height: 100%;
  }
  &__left-side {
    min-height: 100%;
    max-width: 50%;
    flex: 0 1 50%;
    background-position: center;
    background: url("/images/bg_register.png");
    background-repeat: no-repeat;
    background-size: cover;
    display: flex; 
    flex-direction: column;
  }
  &__header h1 {
    color: #ffffff;
    @extend %rb-bold;
    font-size: 18px;
    line-height: 21px;
    padding-left: 15px;
    padding-top: 15px;
  }
  &__headindn-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 150px;
  }
  &__heading-welcome {
    color: #ffffff;
    @extend %rb-regular;
    font-size: 36px;
    line-height: 42px;
  }
  &__heading-subwelcome {
    color: #ffffff;
    @extend %rb-regular;
    font-size: 24px;
    line-height: 28px;
    padding-top: 10px;
  }
  &__footer {
    margin-top: auto;
    text-align: center;
    padding-bottom: 25px;
  }

  &__footer img {
    opacity: 0.2;
    cursor: pointer;
  }
  &__footer img:hover {
    opacity: 1;
  }

  /*  ============  Right side =========== */


 

  /* The Close Button */
 &__right-close {
  color: #aaaaaa;
  float: right;
  font-size: 20px;
  font-weight: bold;
}

 &__right-close:hover {
 color: #000;
  text-decoration: none;
  cursor: pointer;
 }  
 &__right-close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

  &__right-side {
    flex: 0 1 50%;
    min-height: 100%;
    max-width: 50%;
  }
  &__right__logo {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 10px;
  }
  &__right-container {
    margin: 0 auto;
    padding: 10px 30px 10px 30px;
  }
  &__right-form {
    padding-top: 10px;

  }
  &__right-form small {
  
    @extend %rb-regular;
  }
  &__form-group {
    padding-bottom: 20px;
  }
  &__form-group label {
    @extend %rb-regular;
    font-size: 15px;
    line-height: 18px;
    padding-bottom: 15px;
    color: #a3a1a1;
  }
  &__form-group input {
    border: none;
    border-bottom: 1px solid #a3a1a1;
    width: 90%;
    transition-duration: 0.4s;
    font-size: 14px;
    line-height: 16px;
    @extend %rb-bold;
    color: #756f6f;
    padding-bottom: 6px;
    padding-left: 15px;
  }
  &__form-group input::placeholder {
    font-size: 14px;
    line-height: 16px;
    color: #756f6f;
  }
  &__form-group input:focus {
    color: #756f6f;
    outline: none;
  }
  &__form-group input:hover{
    border-bottom: 2px solid #a3a1a1;
    width: 100%;
   
  }

  &__right-form-footer {
    padding-top: 10px;
    display: flex;
    justify-content: space-around;
  }
  &__right-buttn-reg {
    border-radius: 30px;
    border: none;
    cursor: pointer;
    transition-duration: 0.4s;
    padding: 14px 40px;
    @extend %rb-bold;
    margin-right: 15px;
    background: #000000;
    color: #ffff;
  }
  &__right-buttn-reg:hover {
    background: #1b0101;
    transform: translateY(2px);
  }
  &__right-buttn-reg:active {
    transform: translateY(4px);
  }
  &__right-buttn-reg:disabled {
    border: 1px solid #999999;
    background-color: #cccccc;
    color: #666666;
    transform: translateY(0px);
  }
  &__right-f-f-registred {
    padding-top: 15px;
    font-size: 14px;
    line-height: 16px;
    text-decoration-line: underline;
    @extend %rb-regular;
    color: #756f6f;
  }
  &__right-server-err{
    border-radius: 8px;
    background-color: red;

  }
  &__right-server-err div{
    padding: 10px;
    @extend %rb-regular;
    color: white;
    
  }

  &__right-server-load object{
    text-align: center;
  
  }

}

/* Add Animation */
@-webkit-keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

@keyframes animatetop {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
