.small__menu {
    position: absolute;
    display: block;
    z-index: 200;

    &-container {
        width: 150px;
        height: 100px;
        background-color: #000000;
        border-radius: 10px 10px;
    }
    &-header {
        border-radius: 10px 10px 0px 0px;
        padding: 5px;
        background-color: #2a3942;
    }
    &-h-h {
        color: #ffffff;
        padding-left: 10px;
        @extend %rb-bold;
    }
    &-body {
        padding-left: 15px;
        transition: 1s all;
        @extend %rb-regular;
        font-size: 16px;
        color: #fff;
        display: block;
    }
    &-body li {
        padding: 5px;
    }
    &-body li ul {
        background: #1e2b32;
        padding-top: 10px;
    }
    &-body li:hover {
        transition: 1s all;
        color: #2f89fc;
        background-color: rgba(42, 56, 65, 0.82);
        border-top-right-radius: 10px 10px;
        border-bottom-right-radius: 10px 10px;
        cursor: pointer;
    }
    &-body li:hover {
        transition: 1s all;
        color: #2f89fc;
        background-color: rgba(42, 56, 65, 0.82);
        border-top-right-radius: 10px 10px;
        border-bottom-right-radius: 10px 10px;
        cursor: pointer;
    }
}

.icon {
    margin-right: 8px;
}

/* ********* MODAL EDIT ****************/

.edit-modal {
    position: fixed;
    z-index: 300;
    padding-top: 10px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.6);

    &-content {
        position: relative;
        background-color: #fefefe;
        margin: auto;
        padding: 0;
        border: 1px solid #888;
        top: 100px;
        width: 450px;
        height: 300px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
            0 6px 20px 0 rgba(0, 0, 0, 0.19);
        -webkit-animation-name: animatetop1;
        -webkit-animation-duration: 0.4s;
        animation-name: animatetop1;
        animation-duration: 0.4s;
    }
    &-container {
        padding: 20px;
        margin: 0 auto;
    }
    &-container h1 {
        @extend %rb-bold;
        font-size: 18px;
        line-height: 21px;
    }
    &-logo {
        text-align: center;
    }
    &-logo img {
        padding-top: 10px;
    }
    &-form {
        padding-top: 20px;
    }
    &-f {
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    &-f_group {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
    }
    &-f_group label {
        padding-right: 20px;
        @extend %rb-regular;
    }
    &-input {
        vertical-align: middle;
        margin: 5px 10px 5px 0;
        padding: 10px;
        background-color: #fff;
        border: 1px solid #ddd;
    }
    &-buttons {
        padding-top: 15px;
        float: right;
    }
    &-btn-save {
        background-color: black;
        color: #ffffff;
    }
}

/* Add MODAL Animation */
@-webkit-keyframes animatetop1 {
    from {
        top: -300px;
        opacity: 0;
    }
    to {
        top: 100;
        opacity: 1;
    }
}

@keyframes animatetop1 {
    from {
        top: -300px;
        opacity: 0;
    }
    to {
        top: 100;
        opacity: 1;
    }
}
/* ********* MODAL ADD CALENDAR ****************/

.add-modal {
    height: 400px;
    width: 400px;
    &-container {
        padding: 20px;
        margin: 0 auto;
    }
    &__form {
        padding-top: 20px;
    }
    &__f-group {
        padding-bottom: 20px;
    }
    &__f-group label {
        @extend %rb-regular;
        font-size: 15px;
        line-height: 18px;
        padding-bottom: 15px;
        color: #a3a1a1;
    }
    &__f-group input {
        border: none;
        border-bottom: 1px solid #a3a1a1;
        width: 90%;
        transition-duration: 0.4s;
        font-size: 14px;
        line-height: 16px;
        @extend %rb-bold;
        color: #756f6f;
        padding-bottom: 6px;
        padding-left: 15px;
    }
    &__f-group select {
        border: none;
        border-bottom: 1px solid #a3a1a1;
        width: 90%;
        transition-duration: 0.4s;
        font-size: 14px;
        line-height: 16px;
        @extend %rb-bold;
        color: #756f6f;
        background-color: #ffffff;
        padding-bottom: 6px;
        padding-left: 15px;
    }

    &__f-group input::placeholder {
        font-size: 14px;
        line-height: 16px;
        color: #756f6f;
    }
    &__f-group input:focus {
        color: #756f6f;
        outline: none;
    }
    &__f-group input:hover {
        border-bottom: 2px solid #a3a1a1;
        width: 100%;
    }
}

/* HEADER MENU add calendar drop down*/

.header_co__rside {
   
    cursor: pointer;
    &-ddown-content {
        display: none;
        position: absolute;
        background-color: #f9f9f9;
        min-width: 160px;
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
        padding: 12px 16px;
        z-index: 10;
        line-height: normal !important;
    }
    &-ddown-content p {
        color: black;
        padding: 12px 16px;
        text-decoration: none;
        display: block;
    }
    &-ddown-content p:hover {
        background-color: #f1f1f1;
        cursor: pointer;
    }
}

/* HEADER MENU User drop down*/

.header_co__profile {
    cursor: pointer;

    &-d-content {
        display: none;
        position: absolute;
        background-color: #f9f9f9;
        min-width: 160px;
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
        padding: 12px 16px;
        z-index: 10;
        @extend %rb-regular;
        font-size: 15px;
        line-height: normal !important;
        
    }
    &-d-content p {
        color: black;
        padding: 12px 16px;
        text-decoration: none;
        display: block;

        cursor: pointer;
    }
    &-d-content p:hover {
        background-color: #f1f1f1;
    }
}
