
/* NAV BAR AND A SIDE RULES FOR MAIN PAGE CALENDAR */
@import "main_media";



.header_co { grid-area: header; }
.leftmenu { grid-area: menu; }
.main_co { grid-area: main; }


.grid-container {
  display: grid;
  height: 100vh;
  grid-template-areas:
    'menu header header header header header'
    'menu  main main main main main'
    'menu  main main main main main';
    grid-template-columns: 220px;
    grid-template-rows: 50px;
   
 
  
}

.header_co{
  background-color:#fff;
}

.main_co{
 
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.05) 0%, rgba(255, 255, 255, 0) 100%), #FFFFFF; 

  
}
.left_menu-container{
  width:80%;
  margin: 0 auto;
}


.leftmenu{
    background-color: #000000;
    padding-top: 20px;
    color:  #FFFFFF;
    &__pages{
      padding-top: 30px;
     
    }
    &_p-header{
        @extend %rb-regular;
      
        font-size: 20px;
         line-height: 21px;
    }
    &_p-list{
        padding-top: 10px;
        display: inline-block; 
        text-align: left; 
        padding-left: 45px;
       
    }
    &_p-list-item{
        @extend %rb-regular;
        font-size: 16px;
        line-height: 21px;

    }
    &_p-list-item:hover{
      transform: translateX(-2px);
      transition-duration: 0.4s;
      text-decoration:underline;
    }
    &_p-list-item + .leftmenu_p-list-item{
        padding-top: 6px;
    }
  }

  /*       NAV BAR      */

  .header_co{
  
    &__inner{
      display:flex;
      justify-content: space-between;
      
    }
    
    &__leftside{
        display:flex;
    }
    &__leftside-burgermenu{
        padding-top: 12px;
        cursor: pointer;
  
     }
    &__leftside-b__menu-item{
      width: 25px;
      height: 4px;
      background-color: black;
      margin: 3px 0;  
      border-radius: 3px;


    } 

    /* LEFT BURGER MENU HOVER */

    &__rside-menu-ddown:hover .header_co__rside-ddown-content {
      display: block;
    }
    &__leftside-burgermenu:hover .header_co__leftside-b__menu-item {
      background-color: rgb(99, 99, 99);
    }
  
  /* RIGHT MENU HOVER */

  &__profile:hover .header_co__profile-d-content {
    display: block;
    
  }
  &___profile:hover .header_co__profile-d-content {
    background-color: rgb(99, 99, 99);
  }



    &__leftside-clname{
        padding-left: 50px;
        @extend %rb-regular;
        font-size: 18px;
        
        line-height: 50px;
        letter-spacing: 0.155em;

    }
    &__rightside-menu{
        display:flex;
       
    }
    &__profile{
        display: flex;
        padding-left: 20px;
    }

    &__rside-menu-ddown{
        padding-right: 10px;
     
        line-height: 50px;
    }
    &__rside-menu-ddown p{
        font-size: 14px;
        @extend %rb-regular;
     
        color: #898787;

    }
    &__profilepicture{
      padding-top: 10px;
      padding-right: 10px;
    }
    &__profile canvas{
       
        border-radius: 50%;
        background-color: #C4C4C4;
    }
    &__avatar{
        height: 35px;
        width: 35px;

    }
    &__profile-ddown{
        font-size: 14px;
        @extend %rb-Medium;
        
    }
    &__profile{
        line-height: 50px;
    }
  
  }

  
.main_co{
  
    display: flex;
    padding-top: 30px;
     &__month-name{
       text-align: center;
     }
     &__month-name{
        @extend %rb-regular;
        font-size: 16px;
        line-height: 19px;
     }
     &__month-left{
         padding-right: 15px;
     }
     &__month-right{
        padding-left: 15px;
    }
    &__month-right:hover{
     cursor: pointer;
    }
    &__month-left:hover{
    cursor: pointer;
    }
  }

